@use './variables'; 

footer {
  background-color: variables.$dark-gray;
  padding: 30px 40px;

  h2, p, div {
    max-width: 240px;
    margin: 0 auto;
    color: variables.$white;
  }

  h2 {
    font-size: 16px;
    font-weight: 500;
    padding-bottom: 20px;  
  }

  #contact-title {
    padding-bottom: 12px;
  }

  p {
    color: variables.$light-gray;
    font-size: 14px;
    font-weight: 400;
  }

  #email {
    padding: 10px 0 14px 0;
  }

  #contact-container {
    display: flex;
    gap: 8px;
  }

  a img {
    display: block;
    height: 30px;
    width: 30px;
  }
}

// Desktop 1080p

@media screen and (min-width: 1200px) {
  footer {
    padding: 24px calc((100vw - 1200px) / 2);
    position: relative;
    z-index: 9999;
    display: flex;
    justify-content: space-between;
  
    h2, p, div {
      max-width: unset;
      margin: 0;
    }
  
    h2 {
      font-size: 24px;
      font-weight: 500;
      padding-bottom: 20px;  
    }
  
    #contact-title {
      padding-bottom: 14px;
    }
  
    p {
      font-size: 16px;
    }
  
    #email {
      padding: 10px 0 14px 0;
    }
  
    #contact-container {
      gap: 10px;
    }

    #intern-links {
      display: flex;
      gap: 10px;
    }

    .intern-link {
      color: #7F7E83;
      text-decoration: none;
      font-size: 16px;
      padding: 5px;

      &:hover {
        color: variables.$white;
        text-decoration: underline;
      }
    }
  }
}

// tablet

@media screen and (max-width: 1240px) and (min-width: 700px) {
  footer {
    padding: 24px calc((100vw - 600px) / 2);
    position: relative;
    z-index: 9999;
    display: flex;
    justify-content: space-between;
  
    h2, p, div {
      max-width: unset;
      margin: 0;
    }
  
    h2 {
      font-size: 16px;
      font-weight: 500;
      padding-bottom: 20px;  
    }
  
    #contact-title {
      padding-bottom: 14px;
    }
  
    p {
      font-size: 14px;
    }
  
    #email {
      padding: 8px 0 10px 0;
    }
  
    #contact-container {
      gap: 8px;

      a {
        width: 25px;
        height: 25px;
      }
    }

    #intern-links {
      display: flex;
      gap: 6px;
    }

    .intern-link {
      color: #7F7E83;
      text-decoration: none;
      font-size: 14px;
      padding: 5px;

      &:hover {
        color: variables.$white;
        text-decoration: underline;
      }
    }
  }
}