@use './variables';
@use './mixins';

#logo {
  margin: 40px auto;
  height: 48px;
  width: 48px;
  display: block;
}

#landing-section {
  display: flex;
  flex-direction: column;
  gap: 40px;
  margin: 0 auto;
  max-width: 240px;
  padding-bottom: 40px;
}

h1 {
  color: variables.$white;
  font-size: 28px;
  line-height: 34px;
  text-align: center;
  font-weight: 500;
}

#buttons-nav {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.round-button {
  @include mixins.round-button(variables.$dark-orange);
  background-image: linear-gradient(to right, variables.$orange , variables.$orange-2);
}

#see-all-button {
  @include mixins.round-button(variables.$gray, #19191B);
  background-image: none;
  padding: 0 22px 0 0 !important;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 175px;
  gap: 12px;

  p {
    min-width: 110px;
  }

  img {
    background-color: variables.$gray;
    padding: 12px;
    border-radius: 50%;
    display: block;
  }
}

.card {
  @include mixins.card-mobile;
  display: flex;
  gap: 9px;
  align-items: center;
  font-weight: 400;

  p {
    font-size: 12px;
  }
}

// Desktop 1080p

@media screen and (min-width: 1240px) {
  #landing-section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 24px;
    margin: 60px 0 0 0;
    margin-left: calc((100vw - 1200px) / 2);
    max-width: unset;
    padding-bottom: 40px;
  }

  h1 {
    font-size: 40px;
    line-height: 45px;
    text-align: left;
    font-weight: 500;
  }

  #landing-p {
    color: #7F7E83;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 24px;
  }

  #buttons-nav {
    display: flex;
    flex-direction: row-reverse;
    gap: 16px;
  }

  #see-all-button {
    padding: 4px 22px 4px 0 !important;
    min-width: 210px;
    gap: 12px;
    border: 3px solid variables.$gray;
  
    p {
      min-width: 150px;
      font-size: 16px;
      font-weight: 400;
      padding-left: 10px;
    }
  
    img {
      padding: 12px;
      transform: scale(120%)
    }
  }

  #marketplace-button {
    font-size: 16px;
    padding: 12px 40px 12px 18px;
    max-width: unset;

    img {
      width: 24px;
      height: 24px;
    }
  }

  .card {
    @include mixins.card-mobile;
    display: flex;
    gap: 24px;
    align-items: center;
    font-weight: 400;
    max-width: unset;
    margin: 80px 0 48px 0;
  
    p {
      font-size: 16px;
    }
  }

  #phoenix {
    display: block;
    position: absolute;
    top: 0;
    left: calc(100vw - 750px);
    z-index: 9;
    max-width: 712px;
  }
}

// tablet

@media screen and (max-width: 1240px) and (min-width: 700px) {
  #landing-section {
    align-items: flex-start;
    gap: 18px;
    margin: 24px 0 0 0;
    margin-left: calc((100vw - 600px) / 2);
    max-width: unset;
    padding-bottom: 40px;
  }

  h1 {
    font-size: 24px;
    line-height: 28px;
    text-align: left;
  }

  #landing-p {
    color: #7F7E83;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 16px;
  }

  #buttons-nav {
    display: flex;
    flex-direction: row-reverse;
    gap: 10px;
  }

  #see-all-button {
    padding: 2px 22px 2px 0 !important;
    min-width: 210px;
    gap: 12px;
    border: 3px solid variables.$gray;
  
    p {
      min-width: 150px;
      font-size: 14px;
      font-weight: 400;
      padding-left: 8px;
    }
  
    img {
      padding: 10px;
      transform: scale(120%)
    }
  }

  #marketplace-button {
    font-size: 14px;
    padding: 10px 36px 10px 16px;
    max-width: unset;

    img {
      width: 20px;
      height: 20px;
    }
  }

  .card {
    @include mixins.card-mobile;
    display: flex;
    gap: 24px;
    align-items: center;
    font-weight: 400;
    max-width: unset;
    margin: 40px 0 40px 0;
  
    p {
      font-size: 14px;
    }
  }
}