@use './variables';

header {
  background-color: variables.$dark-gray;
  padding: 12px 40px;
  box-shadow: 0px 0px 15px rgba(0,0,0,.3);

  a {
    color: variables.$white;
    text-decoration: none;
    font-size: 12px;
    font-weight: 400;

    display: flex;
    align-items: center;
    gap: 8px;
    padding: 8px 16px 8px 10px;
  }

  nav {
    max-width: 280px;
    margin: 0 auto;

    ul {
      display: flex;
      align-items: center;
      justify-content: space-between;
  
      
      .container-li {
        display: flex;

        a:first-child {
          border: 2px solid;
          border-color: variables.$light-gray;
          border-radius: 100px;
        }
      }
    }
  }
}

#cart-icon {
  position: relative;
  #cart-count {
    position: absolute;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: space-around;
    top: 0;
    right: 5px;
    background-color: variables.$dark-orange;
    height: 16px;
    width: 16px;
    border-radius: 50%;
    font-size: 12px;
    font-weight: 500;
  }
}

// Desktop 1080p

@media screen and (min-width: 1200px) {
  
  header {
    background-color: transparent;
    padding: 24px 0;
    box-shadow: none;

    a {
      font-size: 16px;
  
      gap: 10px;
      padding: 8px 16px 8px 12px;

      img {
        height: 20px;
        width: 20px;
      }
    }
  
    nav {
      max-width: 1200px;
      margin: 0 auto;
    }

    #store-button {
      border: 2px solid;
      border-color: variables.$light-gray;
      border-radius: 100px;
    }

    #header-logo {
      display: block;
      width: 40px;
      height: 40px;
      margin-right: 30px;
    }
  }
  
  #cart-icon {
    img {
      height: 30px;
      width: 30px;
    }
    #cart-count {
      top: 0;
      right: 5px;
      height: 20px;
      width: 20px;
      font-size: 14px;
    }
  }
}

// tablet

@media screen and (max-width: 1240px) and (min-width: 700px) {
    
  header {
    background-color: transparent;
    padding: 24px 0;
    box-shadow: none;

    a {
      font-size: 14px;
  
      gap: 10px;
      padding: 6px 16px 6px 12px;

      img {
        height: 18px;
        width: 18px;
      }
    }
  
    nav {
      max-width: 600px;
    }

    #store-button {
      border: 2px solid;
      border-color: variables.$light-gray;
      border-radius: 100px;
    }

    #header-logo {
      display: block;
      width: 40px;
      height: 40px;
      margin-right: 25px;
    }
  }
  
  #cart-icon {
    img {
      height: 28px;
      width: 28px;
    }
    #cart-count {
      right: 6px;
      height: 18px;
      width: 18px;
      font-size: 14px;
    }
  }
}