@use './variables';

.info {
  background-color: variables.$dark-gray;
  padding: 24px 0;
  width: 100%;
  display: flex;
  gap: 24px;
  flex-direction: column;

  div {
    display: flex;
    max-width: 260px;
    gap: 16px;
    margin: 0 auto;
    align-items: center;

    img {
      display: block;
      height: 25px;
      width: 25px;
    }

    p {
      color: variables.$white;
      min-width: 210px;
      font-size: 14px;
      line-height: 18px;
    }
  }
}

.tankhs {
  width: 100%;
  background-color: variables.$gray;
  padding: 24px 0;

  p {
    color: variables.$white;
    max-width: 212px;
    margin: 0 auto;
    font-size: 14px;
  }
}